import React from 'react'

const Breabcrumb = () => {
  return (
    
    <div className='bc-bg'>
        <div className='breadcrumb'>
            <div className='bc-heading'> Contact Page</div>
            <div className='bc-path'><span>HOME </span>	&nbsp; / &nbsp; <span>PAGE</span> &nbsp; / &nbsp; CONTACT</div>
        </div>
    </div>
    
  )
}

export default Breabcrumb