import React, { useRef } from 'react'

const NavBar = () => {
  const navRef = useRef();

  const showNavbar = () => {
    navRef.current.classList.toggle(
      "responsive_nav"
    );
  };
  
  return (

    <div className='nav-sticky'>
    <header> 
      <div className='nav-logo'><a href='/'><span> <i class="fa-solid fa-screwdriver-wrench"></i> Garage</span> On Call</a></div>

      <nav ref={navRef}>

        <a href='/book-an-appointment'>BOOK SERVICE</a>
        <a href='/our-price'>PRICING</a>
       
        <div href='' className='nav-drop'>
          COMPANY <i class="fa-solid fa-caret-down"></i>
          <div className='nav-drop-content'>
            <div className='nav-d-c-link'>
              <a href='/about-us'>About Us</a>
              <a href="/our-team">Our Team</a>
              <a href='/testimonial'>Testimonial</a>
              <a href='/blogs'>Blogs</a>
              {/* <a>Join US</a> */}
              {/* <a>Career</a> */}
            </div>
          </div>
        </div>

        <a href='/contact-us'>CONTACT</a>
        <button className='nav-btn nav-close-btn' onClick={showNavbar}><i class="fa-solid fa-circle-xmark"></i></button>
      </nav>
      <button className='nav-btn' onClick={showNavbar}><i class="fa-solid fa-bars"></i></button>
      <div className='nav-end'>
 
        <div></div>
        <div className='nav-end-start'>

          <a href='#' className='ld' >Login / Register</a>
          <a href='#' className='sd' ><i class="fa-solid fa-user"></i></a>

        </div>
        <></>
        <div className='nav-end-end'>

          <a href='#' className='ld'><i class="fa-solid fa-cart-shopping"></i></a>
          <a href='#' className='ld'><i class="fa-solid fa-location-dot"></i></a>
          <input className='nav-inp ld' type='text' placeholder='Location....' />
          <a href='#' className='sd' ><i class="fa-solid fa-location-crosshairs"></i></a>
        </div>

      </div>
    </header>
  </div>
    
    
  )
}

export default NavBar